const createBar = (amt) => {
    return (
   
        <svg onMouseEnter={(e)=> setTooltip(e, amt)} onMouseLeave={(e)=>clearToolTip()}  height="20" width='100%'>
        <line x1="0" y1="0" x2="100%" y2="0" style={{stroke:'rgb(71, 71, 71)',strokeWidth:'1.75em'}} />
        <line x1="0" y1="0" x2={amt+"%"} y2="0" style={{stroke:'rgb(0, 206, 161)',strokeWidth:'1.75em'}} />
        </svg>

    );
}


const setTooltip = (e, amt) =>{
    document.getElementById('tooltip').setAttribute("style", "display:inline;top:"+(e.clientY+20)+"px;left:"+e.clientX+"px;")
    document.getElementById('tooltip').innerHTML = "Skill Level: " + amt + "%";
}

const clearToolTip = () => {
    document.getElementById('tooltip').setAttribute("style", "display:none;")
}